<template>
    <div class="wallet-options">
        <div class="window light trigger-block" @click="triggerDropdown">
            <!-- <label style="color: #EAEAEA;">{{$localizeFilter(`exchange_order_type_${options[selectedIndex].replace('-','_')}`)}}</label> -->
            <!-- <label style="color: #EAEAEA;">{{options[selectedIndex].replace('-','_')}}</label> -->
            <label style="color: #EAEAEA;">{{((selectedIndex || selectedIndex==0) ? $store.getters.networkByTicker(options[selectedIndex]).networkName : $localizeFilter('specify_network'))}}</label>
            <button class="trigger-button">
                <img :src="require('@/assets/newImages/double-arrows-white.svg')">
            </button>
        </div>
        <div class="window light dropdown-block" v-if="isOpen">
        <ul>
            <li v-for="(option,idx) in options" :key="idx" :class="selectedIndex==idx ? 'selected' : ''" @click="selectOption(option, idx)">
                <!-- {{option.replace('-','_')}} -->
                {{ $store.getters.networkByTicker(option).networkName }}
            </li>
        </ul>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: [
        'options'
    ],
    data: ()=>({
        isOpen: false ,
        selectedIndex: null,
    }),
    setup() {
        
    },
    methods: {
        triggerDropdown() {
            this.isOpen = !this.isOpen
        },
        selectOption(option, idx) {
            this.selectedIndex = idx
            this.$emit('selectOption', option)
            this.isOpen = false
        }
    }
})
</script>

